.logo-group .logo-path,
.logo-group .logo-dot {
  stroke: inherit;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  stroke-width: 0;
  fill: none;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s; }

.logo-group .logo-path {
  stroke-width: 10;
  -webkit-animation: show-logo-path 1s linear forwards;
          animation: show-logo-path 1s linear forwards; }

.logo-group .logo-dot {
  fill: inherit;
  opacity: 0;
  -webkit-animation: show-logo-dot 1s linear forwards;
          animation: show-logo-dot 1s linear forwards; }

@-webkit-keyframes show-logo-path {
  0% {
    stroke-dashoffset: 1000; }
  75%,
  100% {
    stroke-dashoffset: 650; } }

@keyframes show-logo-path {
  0% {
    stroke-dashoffset: 1000; }
  75%,
  100% {
    stroke-dashoffset: 650; } }

@-webkit-keyframes show-logo-dot {
  0%,
  75% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes show-logo-dot {
  0%,
  75% {
    opacity: 0; }
  100% {
    opacity: 1; } }



