.signature-clip .clip-stroke {
  stroke: inherit;
  stroke-width: 1; }

.signature-stroke {
  stroke: inherit;
  stroke-linecap: round;
  fill: none; }
  .signature-stroke .stroke-J,
  .signature-stroke .stroke-dot,
  .signature-stroke .stroke-W,
  .signature-stroke .stroke-ong {
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
    stroke-width: 0;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s; }



