.module-repo .name {
  font-size: 32px;
  font-weight: lighter;
  letter-spacing: 4px;
  cursor: pointer;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s; }
  .module-repo .name:hover {
    letter-spacing: 10px; }

.module-repo .detail {
  margin: 10px 0px 10px 20px;
  max-height: 20px;
  overflow: hidden;
  border-left: 1px solid;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s; }
  .module-repo .detail.expanded {
    max-height: 300px; }
    .module-repo .detail.expanded .description,
    .module-repo .detail.expanded .link {
