#intro .headline {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }
  #intro .headline .title {
    font-size: 60px; }
  #intro .headline .title,
  #intro .headline .subtitle {
    text-align: center;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    opacity: 0; }
    #intro .headline .title.title,
    #intro .headline .subtitle.title {
      -webkit-animation: slide-up 0.5s 0s forwards;
              animation: slide-up 0.5s 0s forwards; }
    #intro .headline .title.subtitle,
    #intro .headline .subtitle.subtitle {
      -webkit-animation: fade-in 0.5s 0.5s forwards;
              animation: fade-in 0.5s 0.5s forwards; }

@media only screen and (max-width: 650px) {
  #intro .headline .title {
    font-size: 46px; } }



