.shape-wrapper {
  position: absolute;
  z-index: -1;
  opacity: 0;
  -webkit-animation: show-shape 1s forwards;
          animation: show-shape 1s forwards; }
  .shape-wrapper .shape {
    -webkit-transform-origin: top left;
        -ms-transform-origin: top left;
            transform-origin: top left; }
    .shape-wrapper .shape.float1 {
      -webkit-animation: floating1 ease-in-out infinite alternate;
              animation: floating1 ease-in-out infinite alternate; }
    .shape-wrapper .shape.float2 {
      -webkit-animation: floating2 ease-in-out infinite alternate;
              animation: floating2 ease-in-out infinite alternate; }

@-webkit-keyframes show-shape {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes show-shape {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes floating1 {
  0% {
    -webkit-transform: translateX(0%) rotate(0deg);
            transform: translateX(0%) rotate(0deg); }
  100% {
    -webkit-transform: translateX(10%) rotate(30deg);
            transform: translateX(10%) rotate(30deg); } }

@keyframes floating1 {
  0% {
    -webkit-transform: translateX(0%) rotate(0deg);
            transform: translateX(0%) rotate(0deg); }
  100% {
    -webkit-transform: translateX(10%) rotate(30deg);
            transform: translateX(10%) rotate(30deg); } }

@-webkit-keyframes floating2 {
  0% {
    -webkit-transform: translateX(0%) rotate(0deg);
            transform: translateX(0%) rotate(0deg); }
  100% {
    -webkit-transform: translateX(-10%) rotate(-20deg);
            transform: translateX(-10%) rotate(-20deg); } }

@keyframes floating2 {
  0% {
    -webkit-transform: translateX(0%) rotate(0deg);
            transform: translateX(0%) rotate(0deg); }
  100% {
    -webkit-transform: translateX(-10%) rotate(-20deg);
            transform: translateX(-10%) rotate(-20deg); } }



